@import url("./variables.css");
.icon {
  width: 40px;
  margin: 0 5px;
}

/* medium */
@media screen and (min-width: 800px) {
  .icon {
    width: 50px;
    margin: 35px auto;
  }
}

/* large */
@media screen and (min-width: 1200px) {
  .icon {
    width: 80px;
    margin: 50px auto;
  }
}
