.button {
  background: var(--color-three);
  color: black;
  color: var(--color-two);
  transition: linear 0.42s all;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 20px;
  box-shadow: 0 0 5px 0.2px black;
}

.button:hover {
  background: var(--color-five);
  color: white;
  transition: linear 0.42s all;
  cursor: pointer;
}

.button--circle {
  border-radius: 100%;
  height: 80px;
  width: 80px;
}

.button--right {
  visibility: hidden;
  position: absolute;
  z-index: -3;
}

/* medium */
@media screen and (min-width: 950px) {
  .button--right {
    visibility: visible;
    position: fixed;
    right: 20px;
    bottom: 15vh;
    z-index: 4;
    font-size: 2rem;
  }
  .button--right-two {
    bottom: 35vh;
  }
  .button--right-three {
    bottom: 55vh;
  }
  .button--right-four {
    bottom: 75vh;
  }
}
